<template>
    <div class="cumplimiento">
        <div class="info" :style="is_index ? 'height: 0%;' : 'height: 100%;'">
            <transition name="SlideInX">
                <router-view></router-view>
            </transition>
        </div>
        <div class="cumplimiento__boton-container">
            <router-link
                :to="{ name: 'cumplimiento_1' }"
                class="cumplimiento__links"
                >1</router-link
            >
            <router-link
                :to="{ name: 'cumplimiento_2' }"
                class="cumplimiento__links"
                >2</router-link
            >
            <router-link
                :to="{ name: 'cumplimiento_3' }"
                class="cumplimiento__links"
                >3</router-link
            >
            <router-link
                :to="{ name: 'cumplimiento_4' }"
                class="cumplimiento__links"
                >4</router-link
            >
            <router-link
                :to="{ name: 'cumplimiento_5' }"
                class="cumplimiento__links"
                >5</router-link
            >
            <router-link
                :to="{ name: 'cumplimiento_6' }"
                class="cumplimiento__links"
                >6</router-link
            >
        </div>
        <transition name="SlideInX">
            <div class="image-index" v-if="is_mobile || is_index">
                <img
                    class="cumplimiento__foto"
                    src="/assets/images/cumplimiento_foto.jpg"
                    alt="Planeación institucional"
                />
                <p class="cumplimiento__texto">
                    En el año 2020 la labor desplegada por el Consejo de Estado
                    permitió alcanzar grandes logros y obtener resultados
                    estratégicos que fortalecen la institucionalidad y responden
                    a las principales necesidades y expectativas de los usuarios
                    y grupos de valor.
                </p>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    name: "cumplimiento",
    data() {
        return {
            is_mobile: true,
            is_index: true
        }
    },
    methods: {
        handleResize() {
            const isMobile = window.matchMedia(
                "(max-width: 720px), (max-height: 410px)"
            )
            this.is_mobile = isMobile.matches ? true : false
        }
    },
    watch: {
        $route: {
            immediate: true,
            handler() {
                if (this.$route.name == "cumplimiento") {
                    this.is_index = true
                } else {
                    this.is_index = false
                }
            }
        }
    },
    mounted() {
        window.addEventListener("resize", this.handleResize)
        this.handleResize()
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.handleResize)
    }
}
</script>

<style lang="scss" src="./cumplimiento.scss" scoped></style>
